//Global variables and change variable values

@import url('https://fonts.googleapis.com/css?family=Lato:400,700|Nunito:400,700');

$turquoise: #44ddd0;
$purplegrad: #cc5ee2;
$green: #27d7a1;
$purple: #9e0be2;

$light-green: #45f69e;
$dark-green: rgb(0, 100, 80);
$light: #ece6e6;

//Bulma global Variables
$family-sans-serif: "Nunito", sans-serif;
$primary: $purple;

$link:$purple;

//update Bulma component variabnle

$control-border-width: 2px;
$input-border-control: transparent;
$input-shadow: none;

//Costom global style

html{
    background: linear-gradient(90deg, rgba(33,0,36,1) 0%, rgba(48,9,101,1) 47%, rgba(125,5,170,1) 100%);
}
html, body{
    height: 100%;
}

.centered-content{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
}
